

export default function(vm) {
	const http = uni.$u.http
	// import md5Libs from "@/utils/md5";
	const getTokenStorage = () => {
		let token = ''
		try {
			token = uni.getStorageSync('userid')
		} catch (e) {
			//TODO handle the exception
		}
		return token
	}
	let md5flag = false
	let requests = [] // token
	let isRefreshing = false //token
	http.setToken = (token, md5flag) => {
		http.config.header['userid'] = token
		if(!md5flag) {
			// md5userid
			uni.setStorageSync('userid', token) 
		}
		
	}
	function get_xcx_code() {
		return new Promise((resolve, rejected) => {
			uni.login({
				success: function (res){
					resolve(res.code);
				},
				fail: err => {
					md5flag = true
					rejected(err)
				}
			});
		});
	}
	
	async function refreshToken() {
		// tokentoken
		try{
			let code = await get_xcx_code();
			console.log('code:',code)
			return http.post('login_cancel',{code:code})
		}catch(e){
			return e
		}
		
	}
	// 
	http.setConfig((config) => {
		/* config */
		config.baseURL = 'https://wx.sunsirs.cn/Api/'; /*  */
		config.header = {
			...config.header,
			'content-type': 'application/x-www-form-urlencoded',
			'appid': 10000,
			'appsecret': '5406NzMVC6CCMYaDwHzN9pg/fhFF6uaeKwVTbMmNFqHA29dLE78VFJU',
		}
		return config
	})

	// 
	http.interceptors.request.use(async (config) => { // async await 
		// console.log(config)
		const token = getTokenStorage()
		config.header = {
			...config.header,
			'userid': token
		}
		
		// token
		if (config.url.indexOf('login_cancel') >= 0) {
			return config
		}
		if (!token) {
			// token
			if (!isRefreshing) {
				console.log('token ing')
				isRefreshing = true
				refreshToken().then(res => {
					console.log('token',res)
					let userid = ""
					// if(res.errMsg != "request:ok") {
					// 	userid = md5Libs.md5(formatDate(new Date()) + 'wsdz')
					// 	http.setToken(userid, true)
					// }else {
					userid = res.userid
					http.setToken(userid)
					// }
					
					console.log('token')
					requests.forEach(cb => cb(userid))
					// 
					requests = []
				}).catch(res => {
					console.error('refresh token error: ', res)
				}).finally(() => {
					isRefreshing = false
				})
			}
			return new Promise(resolve => {
				requests.push((token) => {
					// configtokentokentoken
					config.header['userid'] = token
					resolve(config)
				})
			})
		
		}
		// dataundefined{}
		// config.data = config.data || {}
		// // customtoken
		// if (config?.custom?.auth) {
		// 	// vmvuexvm.$store.state
		// 	config.header.token = vm.$store.state.userInfo.token
		// }
		return config
	}, config => { // async await 
		return Promise.reject(config)
	})

	// 
	http.interceptors.response.use((response) => {
		uni.hideNavigationBarLoading();
		uni.hideLoading()
		if (response.hasOwnProperty('data')) {
			if (response.data.code != 1) {
				if (response.data.msg) {
					uni.showToast({
						title: response.data.msg,
						icon: 'none',
						mask: true,
						duration: 1000
					})
				}
			}
		} 
		return response.data
	}, (response) => {
		//  statusCode !== 200
		console.log(response)
		return Promise.reject(response)
	})
}
