//    'd'==>''  'h'==>''
const expressUnit = v => {
	if(v == 'd') return ''
	else if(v == 'h') return ''
	else return v
}
//  
const date2timestamp = v => {
	if(!v) return ''
	return new Date(v.replace(/-/g, "/")).getTime()
}
// 1 2
const tradeType = v => {
	if(v == '1') return ''
	else if(v == '2') return ''
	else return v
}
// 1 2
const tradeType2 = v => {
	if(v == '1') return ''
	else if(v == '2') return ''
	else return v
}
//2 
const date2szx = v => {
	if(v == 'ftd') return ''
	else if(v == 'mtd') return ''
	else if(v == 'ltd') return ''
	else return v
}
//B: S: BS:
const settleMode = v => {
	if(v == 'B') return ''
	else if(v == 'S') return ''
	else if(v == 'BS') return ''
	else return v
}

const postType2fbr = v => {
	if(v == '1') return ''
	else return ''
}

const price2str = (price, dprice) => {
	if(price != 0) return price;
	if(!dprice) return ''
	else return ''
}

export default {
	expressUnit,
	date2timestamp,
	tradeType,
	tradeType2,
	settleMode,
	date2szx,
	postType2fbr,
	price2str
}